<template>
  <div class="date-container">
    <h2>{{ sDay }}, {{ sMonth }} {{ number }}<sup>{{ nieme }}</sup></h2>
  </div>
</template>

<script>
export default {
  name: 'Date',
  data () {
    return {
      sDay: '',
      sMonth: '',
      number: 0,
      nieme: ''
    }
  },
  mounted () {
    const time = new Date()
    const day = time.getDay()
    const month = time.getMonth() + 1
    this.number = time.getDate()

    switch (day) {
      case 1:
        this.sDay = 'Monday'
        break
      case 2:
        this.sDay = 'Tuesday'
        break
      case 3:
        this.sDay = 'Wednesday'
        break
      case 4:
        this.sDay = 'Thursday'
        break
      case 5:
        this.sDay = 'Friday'
        break
      case 6:
        this.sDay = 'Saturday'
        break
      case 0:
        this.sDay = 'Sunday'
        break
    }
    switch (month) {
      case 1:
        this.sMonth = 'January'
        break
      case 2:
        this.sMonth = 'February'
        break
      case 3:
        this.sMonth = 'March'
        break
      case 4:
        this.sMonth = 'April'
        break
      case 5:
        this.sMonth = 'May'
        break
      case 6:
        this.sMonth = 'June'
        break
      case 7:
        this.sMonth = 'July'
        break
      case 8:
        this.sMonth = 'August'
        break
      case 9:
        this.sMonth = 'September'
        break
      case 10:
        this.sMonth = 'October'
        break
      case 11:
        this.sMonth = 'November'
        break
      case 12:
        this.sMonth = 'December'
        break
    }
    if (this.number === 1 || this.number === 21 || this.number === 31) {
      this.nieme = 'st'
    } else if (this.number === 2 || this.number === 22) {
      this.nieme = 'nd'
    } else if (this.number === 3 || this.number === 23) {
      this.nieme = 'rd'
    } else {
      this.nieme = 'th'
    }
  }
}
</script>

